@media screen and(max-width: 1400px) {
    .container {
        width: 1100px;
    }
    .section__reviews .card {
        width: 260px;
    }
    h1 {
        font-size: 52px;
    }
    .section:nth-child(1) .section__block .block__text p {
        width: 90%;
    }
    .bonus__section .container__card .card {
        width: 260px;
    }
}
@media screen and(max-width: 1200px) {
    .container {
        width: 1000px;
    }
    .section:nth-child(1) .section__block {
        img {
            width: 100%;
        }
    }
    .section__reviews .card {
        width: 220px;
    }
    .bonus__section .container__card .card {
        width: 100%;

    }
    .section__reviews .block__review {
        width: 95%;
    }
    .section__reviews .card .card__review {
        padding: 20px;
    }
    .section__reviews .card .card__review hr {
        margin: 25px 0px;
        opacity: 0.5;
        border: 1px solid #000000;
    }
    .section__reviews .card .card__review .block__img p {
        text-align: start;
    }
    .developer__container .text__h2 {
        left: 160px;
    }
}
@media screen and(max-width: 1060px) {
    .container {
        width: 740px;
    }
    .section:nth-child(1) .section__block img {
        width: 40%;
    }
    h1 {
        font-size: 48px;
    }
    h2 {
        font-size: 34px;
    }
    p {
        font-size: 15px;
        line-height: 122.9%;
    }
    .section:nth-child(1) .section__block .block__text p {
        width: 95%;
    }
    .developer__container .text__h2 {
        width: 620px;
    }
    .developer__container .text__h2 h2 {
        font-size: 34px;
    }
    .developer__container .text__h2 {
        width: 700px;
        left: 20px;
    }
    .section:nth-child(2) {
        padding-bottom: 80px;
    }
    .bonus__section h2 {
        font-size: 34px;
    }
    .section__reviews .block__review {
        display: none;
    }
    .section__reviews .block__review-tablet {
            display: block;
            display: flex;
            justify-content: space-between;
            width: 100%;
        .card {
            width: 230px;
            height: 335px;
            background: #FFFFFF;
            box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
            border-radius: 10px;
            .card__review {
                padding: 10px;
                .block__img {
                    display: flex;
                    align-items: flex-end;
                    img {
                        margin-right: 20px;
                        border-radius: 60px;
                    }
                    span {
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 600;
                        font-size: 15px;
                        line-height: 20px;
                        text-align: center;
                        color: #000000;
                    }
                    p {
                        font-family: 'Nunito';
                        font-style: normal;
                        font-weight: 300;
                        font-size: 9px;
                        line-height: 12px;
                        text-align: center;   
                        color: #000000;
                        padding: 10px 0px 20px 0px;
                    }
                    .line {
                        border: 2px solid #000000;
                        width: 40px;
                        border-radius: 4px
                    }
                }
                hr {
                    margin: 30px 0px;
                    opacity: 0.5;
                    border: 1px solid #000000;
                }
                p {
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 19px;
                    color: #000000;
                }
            }
        }
        .card:hover {
            box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.25);
            transition: all 0.6s ease-in;
        }
    }
    .bonus__section .container__card .card p {
        font-size: 14px;
    }
    .section:nth-child(2) {
        padding-bottom: 100px;
    }
    .section:nth-child(4) {
        padding-top: 80px;
    }
    .section {
        width: 100%;
        padding-top: 80px;
    }
    .bonus__section .container__card {
        padding-top: 40px;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 30px;
    }
    .section:nth-child(1)._active {
        padding-top: 30px;
    }
}
