        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 10px;
    }
}
@media screen and(max-width: 576px) {
    .container {
        width: 85%;
    }
    .developer__container .text__h2 h2 {
        font-size: 26px;
        padding: 10px 0px 0px 0px;
    }
    .section:nth-child(1) .section__block {
        flex-direction: column;
        align-items: flex-start;
    }
    .section:nth-child(1) .section__block img {
        width: 80%;
    }
    .section:nth-child(1) .section__block .block__text p {
        padding-top: 10px;
        width: 90%;
    }
    .section:nth-child(1) .section__block {
        align-items: end;
    }
    .developer__container .text__h2 {
        width: 100%;
        left: 0px;
        position: static;
        justify-content: center;
    }
    .developer__container .text__h2 h2 {
        font-size: 22px;
        padding: 0px 0px 10px 0px;
        position: static;
        line-height: 27px;
    }
    .developer__container .text__h2 .btn {
        position: static;
        margin: 0 auto;
    }
    .section:nth-child(2) {
        padding-bottom: 0px;
    }
    .section {
        width: 100%;
        padding-top: 40px;
    }
    h2 {
        font-size: 22px;
        font-weight: 700;
    }
    .section:nth-child(4) {
        padding-top: 40px;
    }
    .section__reviews .block__review-phone .card {
        width: 47%;
        height: 350px;
    }
    .section__reviews .block__review-phone .card .card__review .block__img {
        flex-direction: column;
        align-items: start;
        .block__text {
            padding-top: 8px;
            p {
                padding: 6px 0px 10px 0px;
            }
        }
    }
    .section__reviews .block__review-phone .card .card__review .block__img img {
        margin-right: 0px;
        border-radius: 60px;
        width: 95px;
    }
    .section__reviews .block__review-phone .card .card__review hr {
        margin: 15px 0px;
    }
    .section__reviews .block__review-phone .card .card__review p {
        font-size: 10px;
        line-height: 15px;
    }
    .developer__container .accordion__list .accordion .accordion__control {
        padding: 10px 10px;
    }
    .developer__container .accordion__list .accordion .accordion__control .accordion__title {
        font-size: calc(20px + (16 + 16 * 0.7) * (100vw - 320px) / 800);
    }
    .bonus__section h2 {
        font-size: 24px;
        line-height: 28px;
    }
    .bonus__section .container__card {
        padding-top: 20px;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(2, 1fr);
        gap: 10px;
    }
    .bonus__section .container__card .card p {
        font-size: 11px;
        width: 100%;
    }
}