.header {
  width: 100%;
  height: 77px;
  background: #202020;
  border-bottom: 1px solid rgb(255, 255, 255);
  transition: all 0.5s ease-out;
}
.header .header__section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 11px 0px;
}
.header .header__section .header__logo {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header .header__section .header__logo img {
  padding-right: 10px;
}
.header .header__section .header__logo p {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  color: #FFFFFF;
}
.header .header__section .header__navigation {
  display: flex;
  align-items: center;
}
.header .header__section .header__navigation .link {
  font-family: "Nunito";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #FFFFFF;
  padding: 0px 30px;
}

.burger {
  display: none;
}

.burger__menu {
  top: -140px;
  position: absolute;
}

@media screen and (max-width: 1060px) {
  .burger {
    display: block;
    cursor: pointer;
    transition: all 0.3s ease-out;
  }
  .burger .line {
    width: 30px;
    height: 3px;
    border-radius: 4px;
    background-color: #fff;
    transition: all 0.3s ease-out;
  }
  .burger .line:nth-child(even) {
    margin: 6px 0px;
    transition: all 0.3s ease-out;
  }

  .header__logo {
    z-index: 100;
  }

  .header._active {
    background: #202020;
  }

  .burger._active {
    position: relative;
    width: 30px;
    height: 31px;
    z-index: 100;
    overflow: hidden;
  }
  .burger._active .line:nth-child(2) {
    opacity: 0;
    position: absolute;
  }
  .burger._active .line:nth-child(1) {
    top: 10px;
    position: absolute;
    transform: rotate(45deg);
    transition: transform 1300ms ease;
  }
  .burger._active .line:nth-child(3) {
    transform: rotate(135deg);
    transition: transform 900ms ease;
    top: 10px;
    position: absolute;
  }

  .header._active {
    height: 141px;
    transition: all 800ms ease;
    background: #202020;
  }

  .burger__menu._active {
    top: 0px;
    transition: top 800ms ease;
    z-index: 0;
    height: 140px;
    background: #202020;
  }
  .burger__menu._active .burger__container {
    display: flex;
    justify-content: space-between;
    width: 740px;
    margin: 0 auto;
    padding-top: 80px;
  }
  .burger__menu._active .burger__container .burger__navigation {
    display: flex;
    align-items: center;
    position: static;
    top: 90px;
    justify-content: space-between;
  }
  .burger__menu._active .burger__container .burger__navigation .link {
    font-family: "Nunito";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    color: #ffffff;
    padding: 0px 20px;
  }
  .burger__menu._active .burger__container .burger__right p {
    display: none;
  }
  .burger__menu._active .burger__container .burger__right .btn {
    display: block;
    padding: 11px 34px;
  }

  .burger__menu {
    width: 740px;
  }
  .burger__menu .burger__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    transition: padding-top 800ms ease;
    padding-top: 0px;
  }
  .burger__menu .burger__container .burger__navigation .link {
    padding: 0px 10px;
    font-size: 14px;
    font-family: "Nunito";
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;
    color: #ffffff;
  }
  .burger__menu .burger__container .burger__right p {
    display: none;
  }
  .burger__menu .burger__container .burger__right .btn {
    display: block;
