@import "./components/header";
@import "zeroing";
@import "important";
@import "font-face";
@import "mixins";

.container {
    width: 1170px;
    margin: 0 auto;
}
.header .burger__menu._active .burger__container .burger__navigation .link__three {
    color: #FEAB47;
}
.header .header__section .header__navigation .link__three {
    color: #FEAB47;
}
.section__reviews .block__review-tablet {
    display: none;
}
.section__reviews .block__review-phone {
  display: none;
}


.section:nth-child(1) {
    padding-top: 45px;
    .section__block {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        .block__text {
            display: flex;
            flex-direction: column;
            align-items: start;
            h1 {
                width: 100%;
            }
            p {
                line-height: 142.9%;
                padding-top: 40px;
                width: 485px;
            }
        }
    }
}
.section:nth-child(2) {
    padding-bottom: 120px;
}
.developer__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    .text__h2 {
        width: 700px;
        text-align: center;
        display: flex;
        flex-direction: column;
        position: relative;
        left: 240px;
        h2 {
            position: absolute;
            left: 0px;
            right: 0px;
            font-family: "Nunito";
            font-style: normal;
            font-weight: 700;
            font-size: 40px;
            line-height: 55px;
            text-align: center;
            color: #000000;
            padding: 40px 0px 40px 0px;
        }
        .btn {
            width: 240px;
            position: absolute;
            left: 220px;
            right: 0px;
            top: 120px;
        }
    }
    
    .accordion__career {
        padding-top: 40px;
        display: flex;
        flex-direction: column;
      .accordion {
        background: #ffffff;
        box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
        border-radius: 15px;
        margin-bottom: 20px;
        .accordion__control {
          display: flex;
          justify-content: space-between;
          padding: 18px 26px;
          width: 100%;
          overflow: hidden;
          cursor: pointer;
          .accordion__title {
            font-family: "Nunito";
            font-style: normal;
            font-weight: 400;
            font-size: 24px;
            line-height: 33px;
            color: #000000;
          }
          .accordion__icon {
            transition: all 0.6s ease;
          }
        }
        .accordion__content {
          max-height: 0;
          opacity: 0;
          overflow: hidden;
          padding: 0 26px;
          will-change: max-height;
          transition: all 0.3s ease-out;
          box-sizing: content-box;
        }
        .accordion__title {
          font-weight: bold;
        }
        .accordion__control {
          cursor: pointer;
        }
        .accordion__left {
          h4 {
            font-family: "Nunito";
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            color: #000000;
          }
          ul {
            padding: 12px 0px 0px 10px;
            li {
              font-family: "Nunito";
              font-style: normal;
              font-weight: 600;
              font-size: 15px;
              line-height: 20px;
              text-align: start;
              color: #000000;
              padding: 5px 0px;
            }
          }
        }
        .accordion__right {
          position: relative;
          a {
            font-family: "Nunito";
            font-style: normal;
            font-weight: 600;
            font-size: 15px;
            line-height: 20px;
            text-align: start;
            position: absolute;
            bottom: 0px;
            color: #000000;
          }
        }
      }
      .accordion.open {
        .accordion__content {
          display: block;
          overflow: hidden;
          opacity: 1;
          padding: 26px;
          box-sizing: content-box;
          display: flex;
          justify-content: space-between;
          transition: all 0.3s ease-out;
  
          .accordion__left {
            h4 {
              font-family: "Nunito";
              font-style: normal;
              font-weight: 600;
              font-size: 15px;
              line-height: 20px;
              color: #000000;
            }
            ul {
              padding: 12px 0px 0px 10px;
              li {
                font-family: "Nunito";
                font-style: normal;
                font-weight: 600;
                font-size: 15px;
                line-height: 20px;
                text-align: start;
                color: #000000;
                padding: 5px 0px;
              }
            }
          }
          .accordion__right {
            position: relative;
            width: 75px;
            a {
              font-family: "Nunito";
              font-style: normal;
              font-weight: 600;
              font-size: 15px;
              line-height: 20px;
              text-align: start;
              position: absolute;
              color: #000000;
            }
          }
        }
        .accordion__icon {
          transform: rotate(-180deg);
          transition: all 0.6s ease;
        }
      }
    }
}
.section__reviews {
    h2 {
        text-align: center;
        padding-bottom: 60px;
    }
    .slider {
        display: flex;
        position: relative;
        width: 100%;
    }
    .slider__reviews {
        display: flex;
        width: 100%;
    }
    .block__review {
        display: flex;
        width: 100%;
        justify-content: space-between;
    }
    .card {
        width: 270px;
        height: 335px;
        background: #FFFFFF;
        box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
        .card__review {
            padding: 30px;
            .block__img {
                display: flex;
                align-items: flex-end;
                img {
                    margin-right: 20px;
                    border-radius: 60px;
                }
                span {
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 600;
                    font-size: 15px;
                    line-height: 20px;
                    text-align: center;
                    color: #000000;
                }
                p {
                    font-family: 'Nunito';
                    font-style: normal;
                    font-weight: 300;
                    font-size: 9px;
                    line-height: 12px;
                    text-align: center;   
                    color: #000000;
                    padding: 10px 0px 20px 0px;
                }
                .line {
                    border: 2px solid #000000;
                    width: 40px;
                    border-radius: 4px
                }
            }
            hr {
                margin: 30px 0px;
                opacity: 0.5;
                border: 1px solid #000000;
            }
            p {
                font-family: 'Nunito';
                font-style: normal;
                font-weight: 400;
                font-size: 14px;
                line-height: 19px;
                color: #000000;
            }
        }
    }
    .card:hover {
        box-shadow: 0px 2px 9px rgba(0, 0, 0, 0.25);
        transition: all 0.6s ease-in;
    }
}
.section:nth-child(4) {
    padding-top: 120px;
}
.bonus__section {
    h2 {
      font-family: "Nunito";
      font-style: normal;
      font-weight: 700;
      font-size: 40px;
      line-height: 55px;
      text-align: center;
      color: #000000;
    }
    .container__card {
      padding-top: 60px;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(2, 1fr);
      gap: 30px;
      .card {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 270px;
        height: 105px;
        background: #ffffff;
        border-radius: 1px;
        p {
          padding-top: 10px;
          width: 168px;
          font-family: "Nunito";
          font-style: normal;
          font-weight: 600;
          font-size: 15px;
          line-height: 20px;
          text-align: center;
          color: #000000;
        }
      }
    }
}

@import "./components/footer";
@import "breakpoints/breakpoint-careen";

.footer .footer__container .footer__section .footer__navigation .link__three {
    color: #FEAB47;
}
.footer .footer__container-phone .footer__section .footer__section-left .footer__navigation .link__three {
    color: #FEAB47;
}